import './App.css';
import React from 'react';
import { PrimeReactProvider } from 'primereact/api';
import { FullFileBrowser } from 'chonky';
import { setChonkyDefaults } from 'chonky';
import { ChonkyIconFA } from 'chonky-icon-fontawesome';
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import 'prismjs/themes/prism.css'; //Example style, you can use another
import Tree from 'react-d3-tree';
import 'bootstrap/dist/css/bootstrap.min.css';
 import './assets/css/app.scss';


setChonkyDefaults({ iconComponent: ChonkyIconFA });


// This is a simplified example of an org chart with a depth of 2.
// Note how deeper levels are defined recursively via the `children` property.
const orgChart = {
  name: 'CEO',
  children: [
    {
      name: 'Manager',
      attributes: {
        department: 'Production',
      },
      children: [
        {
          name: 'Foreman',
          attributes: {
            department: 'Fabrication',
          },
          children: [
            {
              name: 'Worker',
            },
          ],
        },
        {
          name: 'Foreman',
          attributes: {
            department: 'Assembly',
          },
          children: [
            {
              name: 'Worker',
            },
          ],
        },
      ],
    },
  ],
};

export default function App() {
    const [code, setCode] = React.useState(
    `function add(a, b) {\n  return a + b;\n}`
  );
  return (
    <div className='App'>
      <PrimeReactProvider>
        <div id="app-title" class="container text-center">
          
          <div class="row align-items-start">

            <div id="app-title-logo" class="col">
              <img src='cpyrr/favicon.ico' alt='Cpyrr Logo' width='75' height='75' />
            </div>
            
            <div id="app-title-text" class="col">
              <h1>Compilateur<br /> VM</h1>
            </div> 
          
          </div>
        </div>

        <div id="app-core" class="container-fluid text-center">

          <div id="app-core-row-1" class="row">
            <div id="frame-core-editor" className='frame col'>
              <h2>Code editor</h2>
              <Editor
                value={code}
                onValueChange={code => setCode(code)}
                highlight={code => highlight(code, languages.js)}
                padding={10}
                style={{
                  fontFamily: '"Fira code", "Fira Mono", monospace',
                  fontSize: 12,
                }}
              />  
            </div>

            <div id="frame-tree" className='frame col'>
              <h2>Intermediate tree</h2>
                  {/* // `<Tree />` will fill width/height of its container; in this case `#treeWrapper`. */}
              <div id="treeWrapper" style={{ width: '50em', height: '20em' }}>
                <Tree data={orgChart} />
              </div>
            </div>
         </div>

          
          <div id="app-core-row-2" class="row">

            <div id="frame-files-browser" className='frame col'>
                 <h2>Files browser</h2>
            </div>

            <div id="tools-tab" class="col">
              <div id="frame-buttons" className='container frame'>
                <div class="row">
                  <div class="col">
                    <button type="button" class='btn btn-primary'><span>Compile</span></button>
                  </div>
                  <div class="col">
                    <button type="button" class='btn btn-primary'><span>Run</span></button>
                  </div>
                </div>
              </div>

              <div id="frame-output" className='frame'>
               <h2>Output console</h2>
                <div id="stdout">
                  $...
                </div>
              </div>
            </div>
          </div>

        </div>
      </PrimeReactProvider>
    </div>
  );
}
